@import url('https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap');

@import url('./css/utils.css');

* {
    margin : 0;
    padding : 0;
    box-sizing: border-box;
    font-family: Inter;
}

.App {
    max-width : 800px;
    margin-inline : auto;
    margin-block: 2rem;

    border : 1px solid #ddd;
    border-radius: 1rem;

    overflow: hidden;
}

header {
    display : flex;
    justify-content: space-between;
    padding : 1rem;

    background-color: rgb(255, 184, 184);
}

.btn-add-task {
    padding : 0.3rem 0.6rem;

    border : 0;
    border-radius: 0.5rem;
    background-color: firebrick;
    color : #fff;

    cursor : pointer;
}

.btn-sort {
    padding : 0.3rem 0.6rem;
    margin-right : 0.5rem;

    border : 0;
    border-radius: 0.5rem;
    background-color: darkcyan;
    color : #fff;

    cursor : pointer;
}

.btn-add-task:hover,
.btn-sort:hover {
    background-color: slateblue;
}

.add-task {
    padding-inline : 1rem;
    padding-block: 2rem;
}

.tasks {
    padding-inline : 1rem;
    padding-block: 2rem;
    display : flex;
    flex-direction: column;
    gap : 2rem;
}

label {
    display : grid;
    gap : 0.5rem;
    margin-bottom : 1.5rem;
}

input, textarea, select {
    border : 0;
    outline : 1px solid #ddd;
    padding : 0.3rem 0.6rem;
    width : 100%;
}

select {
    width : 25%;
    min-width: 100px;
}

input:focus, textarea:focus {
    outline : 1px solid darkcyan;
}

textarea {
    resize: none;
    height : 5rem;
}

input[type=submit] {
    padding : 0.5rem 2rem;

    border : 0;
    outline : 0;
    background-color: rgb(255, 184, 184);

    font-size: 1.3rem;
    cursor : pointer;
}

input[type=submit]:hover {
    background-color : darkcyan;
    color : #fff;
}