.hidden {
    display : none;
    animation: hidden 200ms ease-in-out both;
}

.visible {
    display : block;
    animation : visible 250ms ease-in-out both;
    overflow: hidden;
}

@keyframes visible {
    from {
        opacity: 0;
        display : none;
        transform: translateY(-20%);
    }
    10% {
        display: block;
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

